.settings{
    display: flex;
}

.settingsWrapper{
    flex: 9;
    padding: 20px;
}

.settingsTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingsUpdateTitle{
font-size: 30px;
margin-bottom: 20px;
color: lightcoral;
}

.settingsDeleteTitle{
    color: red;
    font-size: 12px;
    cursor: pointer;
}

.settingsForm{
    display: flex;
    flex-direction: column;
}

.settingsPP{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.settingsPP > img{
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover; 
}

.username{
    margin: 10px 0px;
}

.username h1{
    background: teal;
    display:inline-block;
    padding: 10px 20px;
    border-radius: 50%;
    text-transform: uppercase;
    color: aliceblue;

}

.settingsPPIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: lightcoral;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer; 

}

.settingsForm > label {
    font-size: 20px;
    margin-top: 20px;
}

.settingsForm > input{
    color:  gray;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
}

.settingsSubmit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}